<style scoped>
    #header {
        width: 100%;
        /* background-color: darkblue; */
        overflow: hidden;
        padding-bottom: 5px;
    }

    #header #left_logo {
        width: 27px;
        height: 45px;
        float: left;
        display: inline-block;
        margin-left: 42px;
        margin-top: 15px;
        zoom: 0.5;
        background: url("../assets/images/i_head5.png") no-repeat -32px -25px;
    }

    #header h1 {
        width: 30%;
        float: right;
        margin-right: 115px;
        margin-top: 9px;
    }

    .showimg img {
        width: 100%;
        margin-top: 5px;
    }

    .search {
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        background-color: rgba(0, 0, 0, 0.144);
        border-radius: 4px;
        margin-top: 10px;
        padding-top: 10px;
        position: relative;
    }

    .search #search_inp {
        width: 83%;
        background-color:rgba(0, 0, 0, -0.856);
        border: none;
        margin-left: 24px;
    }

    #search_span::before {
        content: "";
        width: 36px;
        height: 36px;
        display: inline-block;
        position: absolute;
        top:14px;
        left: 2.5%;
        zoom: 0.5;
        background: url("../assets/images/ico5.png") no-repeat 0px 0px;
    }

    .con_ul {
        width: 100%;
        margin-top: 10px;
        border-top: 1px solid rgba(0, 0, 0, 0.144);
        /* background-color: crimson; */
        overflow: hidden;
    }

    .con_ul li {
        width: 100%; 
        padding-bottom: 5px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.144);
    }

    .con_ul h2 {
        font-size: 19px;
    }

    .leftword {
        width: 141px;
        /* background-color: darkcyan; */
        margin-top:10px;
    }

    .leftword img {
        width: 20px;
        height: auto;
        margin-left: 3px;
        border-radius: 50%;
        margin-top:4px;
    }

    .mid_content {
        width: 100%;
        margin-top: 10px;
        /* background-color: darkmagenta; */
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .mid_content img {
        float: left;
        width: 40%;
    }

    .mid_content p {
        width: 59%;
        float: right;
        word-break: break-all;
        display: -webkit-box;
        line-height: 26px;
        /* text-align: right; */
        /** */
        -webkit-box-orient: vertical;
        /****/
        -webkit-line-clamp:4;
        /****/
        overflow: hidden;
        /****/
    }
.bot_content{
    width:100%;
}   
    #right_a{
        width:75%;
        display:block;
        float: right;
        text-decoration: none;
        color:gray;
        text-align: right;
    }
   
    .bot_content{
        width:100%;
        overflow: hidden;
        margin-top:10px;
    }
    .bot_content b{
       font-weight: normal;
    }
    .bot_content i{
        color:orange
    }
    .bot_content #ll::after{
        content: "";
        width:32px;
        height:10px;
        /* margin-left:5px;
        margin-right:5px; */
        display: inline-block;
    }
    .bot_content span,.bot_content b,.bot_content i{
        font-size: 14px;
        font-style: normal;
    }
    .bot_content b::after{
        content: "";
        /* margin-left:2px; */
        /* margin-right:2px; */
        display: inline-block;
        font-weight: normal;

    }
    .bot_content b::before{
        content: "";
        width:5px;
        height:5px;
        /* margin-left:2px;
        margin-right:2px; */
        display: inline-block;
        background-color: gray;
    }
    .bot_content i::after{
        content: "";
        color:orange;
        margin-left:2px;
        font-style: normal;
        /* margin-right:2px; */
        display: inline-block;
    }
    .bot_content i::before{
        content: "";
        width:5px;
        height:5px;
        margin-left:2px;
        margin-right:2px;
        display: inline-block;
        background-color: gray;
    }
    .bot_content #global{
        text-decoration: none;
    }
    .li_outside{
        width:92%;
        margin-top:10px;
        margin-right:auto;
        margin-left: auto;
    }
    #bot{
        width:80%;
        margin: auto;
        background-color: rgba(128, 128, 128, 0.24);
        padding-top:10px;
        padding-bottom:10px;
        text-align: center;
        margin: 8%;
        border-radius: 5px;
    }
</style>


